<script>
import { Row, Col, Form, Input, Select, Option } from 'element-ui';
import { FilterDialog } from '../dialog';
export default {
    components: {
        elRow: Row,
        elCol: Col,
        elForm: Form,
        elInput: Input,
        elSelect: Select,
        elOption: Option,
        FilterDialog,
    },
    data() {
        return {
            filterOption: {
                workNo: null,
                workType: null, // 作品分类 0:图画 1:作文 2:视频
                workStatus: null, // 作品状态 0:待审核 1:已通过 2:已驳回 3:已下架
                school: null, // 学校编号
                updateDate: null, // 更新时间
            },
            filterStyle: null, // 筛选方式 0:按时间 1:按学校
        };
    },
    methods: {
        inputHandle(v) {
            // console.log(v);
            this.emitFilterOption();
        },
        selectHandle(type, v) {
            if (type === 'filterStyle') {
                if (v !== '') {
                    this.$refs.FilterDialog.init(v);
                } else {
                    this.filterOption.school = null;
                    this.filterOption.updateDate = null;
                    this.emitFilterOption();
                }
            } else {
                this.emitFilterOption();
            }
        },
        emitFilterOption() {
            this.$emit('filterRusult', this.filterOption);
        },

        handleResult(v) {
            if (v.type === 'school') {
                this.filterStyle = v.value;
                this.filterOption.school = v.value;
                this.filterOption.updateDate = null;
            } else {
                this.filterStyle = v.value;
                this.filterOption.updateDate = v.value;
                this.filterOption.school = null;
            }
            this.emitFilterOption();
        },
    },
};
</script>
<template>
    <el-form class="filter-tool-bar">
        <el-row :gutter="33">
            <el-col :span="5">
                <el-input
                    class="fixed-width"
                    v-model="filterOption.workNo"
                    clearable
                    placeholder="请输入作品编号"
                    style="height: 34px"
                    @change="inputHandle"
                >
                    <i
                        slot="suffix"
                        class="el-input__icon el-icon-search"
                        style="color: #e6745e; font-size: 22px"
                    ></i>
                </el-input>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-select
                    class="fixed-width"
                    v-model="filterOption.workType"
                    placeholder="选择作品类型"
                    clearable
                    @change="selectHandle('workType', $event)"
                >
                    <el-option label="书画" value="0"></el-option>
                    <el-option label="作文" value="1"></el-option>
                    <el-option label="视频" value="2"></el-option>
                </el-select>
            </el-col>
            <el-col :span="6">
                <el-select
                    class="fixed-width"
                    v-model="filterOption.workStatus"
                    placeholder="选择作品状态"
                    clearable
                    @change="selectHandle('workStatus', $event)"
                >
                    <el-option label="待审核" value="0"></el-option>
                    <el-option label="已通过" value="1"></el-option>
                    <el-option label="已驳回" value="2"></el-option>
                    <el-option label="已下架" value="3"></el-option>
                </el-select>
            </el-col>
            <el-col :span="6">
                <el-select
                    class="fixed-width"
                    v-model="filterStyle"
                    placeholder="筛选方式"
                    clearable
                    @change="selectHandle('filterStyle', $event)"
                >
                    <el-option label="选择日期" value="0"></el-option>
                    <el-option label="选择学校" value="1"></el-option>
                </el-select>
            </el-col>
        </el-row>

        <FilterDialog ref="FilterDialog" @handleResult="handleResult" />
    </el-form>
</template>

<style lang="scss" scoped>
.fixed-width {
    width: 100%;
    width: 247px;
    height: calc(56px / 2);
    background: #ffffff;
    border: 2px solid #bcbcbc;
    border-radius: 0;
    ::v-deep .el-input__inner {
        height: 100%;
        border: 0;
    }
    ::v-deep .el-input {
        height: 100%;
        .el-input__inner {
            height: 100%;
            border: 0;
        }
    }
}
</style>
