<script>
import { Container, Tabs, TabPane } from 'element-ui';
import { WorkComp } from './components';
export default {
    name: 'HomeView',
    components: {
        elContainer: Container,
        // elAside: Aside,
        // elMain: Main,
        elTabs: Tabs,
        elTabPane: TabPane,
        WorkComp,
    },
    data() {
        return {
            tabs: ['审批中心', '当前作品'],
            postData: {
                approvalType: '0', // 审批分类 0:待审批 1:当前
                workNo: null, // 作品编号
                workType: null, // 作品分类 0:图画 1:作文 2:视频
                workStatus: null, // 作品状态 0:待审核 1:已通过 2:已驳回 3:已下架
                school: null, // 学校编号
                updateDate: null, // 更新日期
            },
            listData: [],
        };
    },
    computed: {
        workType: function () {
            let str = '';
            if (this.postData.workType) {
                switch (this.postData.workType) {
                    case '0':
                        str = '-书画';
                        break;
                    case '1':
                        str = '-作文';
                        break;
                    default:
                        str = '-视频';
                }
            }
            return str;
        },
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            // 剔除无效请求条件
            const data = {};
            for (const i in this.postData) {
                if (this.postData[i] !== null && this.postData[i] !== '') {
                    data[i] = this.postData[i];
                }
            }
            this.$store
                .dispatch('portfolio/queryList', data)
                .then((res) => {
                    this.listData = res.data.map((item) => {
                        item.isChecked = false;
                        return item;
                    });
                })
                .catch((error) => {
                    console.log(error);
                });
        },
        tabClickHandle(tab, event) {
            this.postData.approvalType = tab.index;
            this.getData();
        },
        // 获取筛选条件
        filterRusult(v) {
            for (const i in this.postData) {
                for (const j in v) {
                    if (i === j) {
                        this.postData[i] = v[j];
                    }
                }
            }
            this.getData();
        },
    },
};
</script>

<template>
    <el-container class="container">
        <el-tabs
            class="custom-tabs"
            tab-position="left"
            type="border-card"
            v-model="postData.approvalType"
            @tab-click="tabClickHandle"
        >
            <el-tab-pane v-for="(item, index) in tabs" :key="item" :name="index + ''">
                <span slot="label">
                    {{ `${index === 0 ? '审批中心' + workType : '当前作品'}` }}
                    <img class="btn-bg" src="@images/btn_bg.png" alt="图片背景" />
                </span>
                <WorkComp
                    v-if="listData.length > 0"
                    :source="listData"
                    :approvalType="postData.approvalType"
                    @filterRusult="filterRusult"
                />
            </el-tab-pane>
        </el-tabs>
    </el-container>
</template>

