<script>
// import { parseTime } from '@/utils';
const yearArr = [];
for (let i = 1970; i <= new Date().getFullYear(); i++) {
    yearArr.push(i);
}
const monthArr = [];
for (let i = 1; i <= 12; i++) {
    monthArr.push(i);
}
export default {
    data() {
        return {
            weekDays: ['一', '二', '三', '四', '五', '六', '日'],
            time: {
                date: null,
                year: new Date().getFullYear(),
                month: new Date().getMonth(),
                day: new Date().getDate(),
            },
            currentSelected: null, // 当前选中的日期
            yearArr: null,
            monthArr: null,
        };
    },
    computed: {
        // 获取当前月份显示日历
        visibleCalendar: function () {
            const calendarArr = [];
            // 获取当前月份的第一天
            const currentFirstDay = new Date(this.time.year, this.time.month, 1);
            // console.log(currentFirstDay);
            // 获取第一天是周几，注意周日的时候getDay()返回的是0，要做特殊处理
            const weekDay = currentFirstDay.getDay() === 0 ? 7 : currentFirstDay.getDay();
            // console.log(weekDay);
            // 用当前月份第一天减去周几前面几天，就是看见的日历的第一天
            const startDay = currentFirstDay - (weekDay - 1) * 24 * 3600 * 1000;
            // console.log(new Date(startDay));
            // 我们统一用42天来显示当前显示日历
            for (let i = 0; i < 42; i++) {
                const date = new Date(startDay + i * 24 * 3600 * 1000);
                calendarArr.push({
                    date: new Date(startDay + i * 24 * 3600 * 1000),
                    year: date.getFullYear(),
                    month: date.getMonth(),
                    day: date.getDate(),
                });
            }
            return calendarArr;
        },
    },
    methods: {
        // 初始化年月数据
        initYearAndMonth(type) {
            const thisObj = this;
            const data = type === 'year' ? yearArr : monthArr;
            // 判断当前点击的年月是否已有数据，有代表重复点击，应该关闭控件
            if (this[`${type}Arr`] && this[`${type}Arr`].length > 0) {
                this[`${type}Arr`] = null;
                return;
            }
            // 给年、月赋值
            this[`${type}Arr`] = data;
            // 判断当前点击的是年还是月，置空另一个
            this[`${type === 'year' ? 'month' : 'year'}Arr`] = null;
            // 立即执行函数 延迟执行滚动条的操作
            (function delayedCalls(type) {
                setTimeout(() => {
                    let scrollTop = 0;
                    data.forEach((item, index) => {
                        if (item === thisObj.time[type] + (type === 'month' ? 1 : 0)) {
                            scrollTop = (index - 2) * 40;
                        }
                    });
                    thisObj.$refs[`${type}Scroll`].scrollTop = scrollTop;
                }, 50);
            })(type);
        },
        // 点击年、月获得选中结果
        selectResultHandle(type, value) {
            this.time[type] = type === 'month' ? value - 1 : value;
            this[`${type}Arr`] = null;
            this.time.day = null;
        },
        // 选中日期
        selectDay(item) {
            // console.log(item.month, this.time.month);
            if (item.month === this.time.month) {
                this.time = item;
                this.$emit('selectResult', {
                    year: this.time.year,
                    month: this.time.month + 1,
                    day: this.time.day,
                });
            }
        },
    },
};
</script>

<template>
    <div class="calendar">
        <ul class="year" @click="yearArr = null">
            <li @click.stop="initYearAndMonth('year')">{{ time.year }}年</li>
            <ul class="year-scroll" :class="{ active: yearArr }" ref="yearScroll">
                <li
                    v-for="(item, index) in yearArr"
                    :class="{ highlight: time.year === item }"
                    :key="index"
                    v-text="item"
                    @click.stop="selectResultHandle('year', item)"
                ></li>
            </ul>
        </ul>
        <ul class="month" @click="monthArr = null">
            <li @click.stop="initYearAndMonth('month')">{{ time.month + 1 }}月</li>
            <ul class="month-scroll" :class="{ active: monthArr }" ref="monthScroll">
                <li
                    v-for="(item, index) in monthArr"
                    :class="{ highlight: time.month + 1 === item }"
                    :key="index"
                    v-text="item"
                    @click.stop="selectResultHandle('month', item)"
                ></li>
            </ul>
        </ul>
        <ul class="head">
            <li v-for="(item, index) in weekDays" :key="index" v-text="item"></li>
        </ul>
        <ul class="body">
            <li v-for="(item, index) in visibleCalendar" :key="index">
                <span
                    :class="{
                        highlight: item.month === time.month,
                        active:
                            item.date === time.date ||
                            (item.month === time.month && item.day === time.day),
                    }"
                    v-text="item.day"
                    @click="selectDay(item)"
                ></span>
            </li>
        </ul>
    </div>
</template>

<style lang="scss" scoped>
.calendar {
    position: relative;
    ul {
        display: flex;
        list-style: none;
        padding: 0;
        justify-content: space-between;
        flex-wrap: wrap;
        li {
            height: 40px;
            font-family: 'Inter';
            line-height: 40px;
            text-align: center;
            letter-spacing: 0.02em;
            color: rgba(0, 0, 0, 0.2);
            flex: 0 0 calc(100% / 7);
            span {
                display: inline-block;
                border-radius: 50%;
                width: 28px;
                height: 28px;
                line-height: 28px;
                cursor: pointer;
                &.highlight {
                    color: #666;
                    &.active {
                        color: #fff;
                        background: rgba(0, 216, 254, 1);
                    }
                    &:hover {
                        color: #fff;
                        background: rgba(0, 216, 254, 0.4);
                    }
                }
            }
        }
    }
    .year,
    .month {
        // overflow: hidden;
        position: absolute;
        top: -70px;
        right: 200px;
        margin: 0;
        box-sizing: border-box;
        border: 1px solid #bcbcbc;
        width: 160px;
        height: 40px;
        font-family: 'Inter';
        font-size: 25px;
        font-weight: 400;
        font-style: normal;
        text-align: center;
        letter-spacing: 0.02em;
        background: #fff;
        cursor: pointer;
        flex-direction: column;
        flex-wrap: nowrap;
        & li {
            color: #666;
        }
        .year-scroll,
        .month-scroll {
            position: absolute;
            top: 46px;
            flex-direction: column;
            flex-wrap: nowrap;
            overflow: hidden;
            overflow-y: auto;
            width: 100%;
            height: 0;
            transition: height 1s ease-in-out;
            background: #ffffff;
            box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
            border-radius: 5px;
            & li {
                color: rgba(102, 102, 102, 0.2);
                &:hover,
                &.highlight {
                    color: #666;
                }
            }
            &.active {
                height: 150px;
            }
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }
    }
    .year {
        right: 440px;
    }
    .head {
        li {
            font-size: 18px;
            font-weight: 600;
        }
    }
    .body {
        li {
            font-size: 14px;
        }
    }
}
</style>
