import { queryList } from '@/api/portfolio';
const state = {
    portfolios: [],
};

const mutations = {
    SET_DATA: (state, data) => {
        state.portfolios = data;
    },
};

const actions = {
    queryList({ commit }, queryConditions) {
        // console.log(queryConditions);
        return new Promise((resolve, reject) => {
            queryList(queryConditions)
                .then((response) => {
                    resolve(response);
                })
                .catch((error) => {
                    reject(error);
                });
        });
    },
};

export default {
    namespaced: true,
    state,
    mutations,
    actions,
};
