<script>
import { Dialog, Button, Row, Col, Form, FormItem, Input } from 'element-ui';
export default {
    components: {
        elDialog: Dialog,
        elButton: Button,
        elRow: Row,
        elCol: Col,
        elForm: Form,
        elFormItem: FormItem,
        elInput: Input,
    },
    props: {
        approvalType: {
            type: String,
            default: () => {
                return '0';
            },
        },
    },
    data() {
        return {
            dialogVisible: false,
            rawData: {
                thumbnail: '',
                workName: '',
                author: '',
                school: '',
                grade: null,
                operator: '',
                operationTime: '',
                workStatus: null,
            },
            detailInfo: Object.assign({}, this.rawData),
        };
    },
    computed: {
        workStatus: function () {
            let str = '未审核';
            switch (this.detailInfo.workStatus) {
                case 0:
                    str = '未审核';
                    break;
                case 1:
                    str = '已通过';
                    break;
                case 2:
                    str = '已驳回';
                    break;
                default:
                    str = '已下架';
            }
            return str;
        },
    },
    methods: {
        init(data) {
            this.dialogVisible = true;
            this.detailInfo = data;
        },
        handleClose() {
            this.dialogVisible = false;
            this.detailInfo = this.rawData;
        },
    },
};
</script>

<template>
    <el-dialog
        class="details-info"
        :modal="false"
        :visible.sync="dialogVisible"
        width="50.5%"
        center
        :before-close="handleClose"
    >
        <span class="title" slot="title">作品详情</span>
        <el-row :gutter="30">
            <el-col :span="10" style="text-align: center">
                <!-- 图画 -->
                <img
                    v-if="detailInfo.workType === 0"
                    class="thumbnail"
                    v-lazy="detailInfo.thumbnail"
                    alt="作品缩略图"
                />
                <!--作文-->
                <div v-if="detailInfo.workType === 1" class="action-btn-group">
                    <div style="width: 100%; height: 80px">
                        <el-button class="actionBtn" type="primary">下载WORD源文件</el-button>
                    </div>
                    <div style="width: 100%; height: 80px">
                        <el-button class="actionBtn" type="primary">查看图片</el-button>
                    </div>
                    <div style="width: 100%; height: 80px">
                        <el-button class="actionBtn" type="primary">查看文字</el-button>
                    </div>
                </div>
                <!--视频-->
                <div v-if="detailInfo.workType === 2" class="action-btn-group">
                    <el-button class="actionBtn" type="primary">播放视频</el-button>
                    <el-button class="actionBtn" type="primary">下载源文件</el-button>
                </div>
                <span
                    class="status-btn"
                    :class="[
                        detailInfo.workStatus === 0
                            ? ''
                            : detailInfo.workStatus === 1
                            ? 'success'
                            : 'error',
                    ]"
                    v-text="workStatus"
                ></span>
            </el-col>
            <el-col :span="14">
                <el-form class="details-form" inline label-width="102px" label-position="left">
                    <el-form-item class="details-form-item">
                        <span slot="label">作&nbsp;品&nbsp;名 :</span>
                        <el-input :value="detailInfo.workName" readonly />
                    </el-form-item>
                    <el-form-item class="details-form-item">
                        <span slot="label">作&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;者 :</span>
                        <el-input :value="detailInfo.author" readonly />
                    </el-form-item>
                    <el-form-item class="details-form-item">
                        <span slot="label">学&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;校 :</span>
                        <el-input :value="detailInfo.school" readonly />
                    </el-form-item>
                    <el-form-item class="details-form-item">
                        <span slot="label">年&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;级 :</span>
                        <el-input :value="detailInfo.grade" readonly />
                    </el-form-item>
                    <template v-if="detailInfo.workStatus === 3">
                        <el-row>
                            <el-col :span="12">
                                <el-form-item class="details-form-item">
                                    <span slot="label">操作人1 :</span>
                                    <el-input :value="detailInfo.operator" readonly />
                                </el-form-item>
                                <el-form-item class="details-form-item">
                                    <span slot="label">操作时间1 :</span>
                                    <el-input :value="detailInfo.operationTime" readonly />
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item class="details-form-item">
                                    <span slot="label">操作人2 :</span>
                                    <el-input :value="detailInfo.operator" readonly />
                                </el-form-item>
                                <el-form-item class="details-form-item">
                                    <span slot="label">操作时间2 :</span>
                                    <el-input :value="detailInfo.operationTime" readonly />
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </template>
                    <template v-else>
                        <el-form-item class="details-form-item">
                            <span slot="label">操&nbsp;作&nbsp;人 :</span>
                            <el-input :value="detailInfo.operator" readonly />
                        </el-form-item>
                        <el-form-item class="details-form-item">
                            <span slot="label">操作时间 :</span>
                            <el-input :value="detailInfo.operationTime" readonly />
                        </el-form-item>
                    </template>
                    <el-form-item class="details-form-item" v-if="detailInfo.workStatus === 2">
                        <span slot="label">驳回理由 :</span>
                        <el-input :value="detailInfo.workName" readonly />
                    </el-form-item>
                    <el-form-item class="details-form-item" v-if="detailInfo.workStatus === 3">
                        <span slot="label">下架理由 :</span>
                        <el-input :value="detailInfo.workName" readonly />
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </el-dialog>
</template>

<style lang="scss" scoped>
.details-info {
    ::v-deep .el-dialog__header {
        padding: 25px 70px 0 70px;
        text-align: left;

        .el-dialog__headerbtn {
            font-size: 40px;
        }
        .title {
            border-radius: 20px;
            padding: 5px 25px;

            // width: 160px;
            // height: 40px;

            font-family: 'Inter';

            // font-size: 25px;
            font-size: 16px;
            font-weight: 400;
            font-style: normal;
            line-height: 30px;
            text-align: center;
            letter-spacing: 0.02em;
            color: #fff;
            background: #93cbda;
        }
    }
    ::v-deep .el-dialog__body {
        padding: 25px 70px 30px 70px;
    }
    .thumbnail {
        display: block;

        // margin: 0 auto;

        border-radius: 10px;
        width: 330px;
        max-width: 320px;
        height: 320px;
        box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.1);
        object-fit: cover;
    }
    .actionBtn {
        position: relative;
        margin: 0 auto;
        border: 0;
        border-radius: 50px;
        width: 232px;
        height: 60px;
        max-height: 60px !important;

        font-family: 'Inter';
        font-size: 25px;
        font-weight: 400;
        font-style: normal;
        line-height: 30px;
        text-align: center;
        letter-spacing: 0.02em;
        color: #fff;
        background-color: #93cbda;
        background-image: url('../../assets/images/btn_bg.png');
        background-repeat: no-repeat;
        background-position: 67px 6px;
        background-size: 232px 84px;
        &::before {
            position: absolute;
            top: 50%;
            left: 50%;
            border: 1px solid rgba(255, 255, 255, 0.5);
            border-radius: 50px;
            width: 224px;
            height: 54px;
            transform: translate(-50%, -50%);
            content: '';
        }
    }
    .action-btn-group {
        display: flex;
        height: 320px;
        justify-content: space-evenly; /* 水平均匀分布 */
        align-items: center; /* 垂直居中 */
        flex-direction: column;
        .el-button {
            display: block;
            padding: 0;
            max-height: 30px;
            flex: 1;
        }
    }
    .details-form {
        ::v-deep .details-form-item {
            margin: 3px 0 0 0;
            width: 100%;
            height: 55px;
            .el-form-item__label {
                font-family: 'Inter';

                // font-size: 25px;
                font-size: 16px;
                font-weight: 400;
                font-style: normal;
                line-height: 30px;
                text-align: center;
                letter-spacing: 0.02em;
                color: #788997;
            }
            .el-form-item__content {
                width: calc(100% - 102px);
                .el-input__inner {
                    border: 0;
                    border-bottom: 1px solid #d3e7ed;
                    border-radius: 0;
                    &:hover {
                        cursor: auto;
                    }
                }
            }
        }
    }
    .status-btn {
        display: inline-block;
        border: 4px solid #bcbcbc;
        width: 121px;
        height: 41px;
        margin: 20px auto 0 auto;
        font-family: 'Inter';
        font-size: 25px;
        font-weight: 700;
        font-style: normal;
        line-height: 41px;
        text-align: center;
        letter-spacing: 0.2em;
        color: #bcbcbc;
        &.success {
            color: rgba(60, 196, 220, 0.5);
            border-color: rgba(60, 196, 220, 0.5);
        }
        &.error {
            color: rgba(230, 116, 94, 0.5);
            border-color: rgba(230, 116, 94, 0.5);
        }
    }
}
</style>
